import styled from 'styled-components';
import { colors } from '../../utils/colors';
import { device } from '../../utils/layoutBreakpoints';

export const StyledGetDemoFormContainer = styled.div`
  font-size: 0.8rem;
  line-height: 1.1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 15px;
  width: 100%;
  label {
    font-weight: 500;
    display: flex;
    flex-direction: column;
    gap: 5px;
    width: 100%;
  }

  input,
  textarea {
    width: 100%;
    box-sizing: border-box;
    background-color: ${colors.primary.white};
    color: ${colors.primary.black};
    border: 1px solid ${colors.secondary.gray300};
    font-family: 'Rubik';
    border-radius: 5px;
    &::placeholder {
      color: ${colors.primary.gray500};
      font-family: 'Rubik';
    }
    &:focus {
      outline: none;
      border: 1px solid ${colors.primary.blue};
    }
  }

  input {
    height: 36px;
    padding: 10px 15px;
  }

  textarea {
    height: 100px;
    padding: 10px 15px;
    resize: none;
  }

  fieldset {
    margin-bottom: 30px;
    display: flex;
    flex-direction: column;
    width: 100%;
    gap: 10px;
    legend {
      font-weight: 500;
      margin-bottom: 10px;
    }
    label {
      cursor: pointer;
      background: ${colors.primary.white};
      border: 1px solid ${colors.secondary.gray300};
      border-radius: 8px;
      padding: 10px 15px;
      text-align: center;
      box-sizing: border-box;
    }
    input {
      display: none;
      &:checked + label {
        color: ${colors.primary.white};
        background-color: ${colors.primary.blue};
      }
    }

    @media ${device.tablet} {
      flex-direction: row;
    }
  }
`;

export const StyledGetDemoContainer = styled.div`
  width: 100%;
  display: flex;
  background-color: ${colors.secondary.gray100};
  border-radius: 16px;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px;
  position: relative;
  z-index: 2;
  min-height: 580px;

  & > div:first-child {
    padding: 25px 25px;
    width: 100%;
    box-sizing: border-box;
  }

  @media ${device.tablet} {
    & > div:first-child {
      padding: 50px 50px;
    }
  }

`;

export const GetDemoBackgroundImagesWrapper = styled.div`
  display: none;
  width: 50%;
  height: 100%;
  @media ${device.laptop} {
    display: block;
  }
`;

export const GetDemoBackgroundImages = styled.div`
  position: relative;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  & > div {
    max-width: 300px;
    height: auto;
    position: absolute;
    &:first-child {
      right: 200px;
    }
    &:nth-child(2) {
      right: -40px;
      top: 250px;
    }
    &:nth-child(3) {
      top: 300px;
      left: -100px;
      z-index: 1;
    }
    &:nth-child(4) {
      right: 80px;
      top: 580px;
    }
  }
`;

export const GetDemoPageWrapper = styled.div`
  width: 100%;
  min-height: 100vh;
  height: 100%;
  background-color: ${colors.secondary.azureishWhite};
`;

export const GetDemoContentContainer = styled.div`
  max-width: 1268px;
  margin: auto;
  padding: 30px 40px 80px;
  display: flex;
  height: 100%;
  & > div:first-child {
    width: 100%;
    @media ${device.laptop} {
        width: auto;
    }
  }
`;

export const GetDemoPageHeader = styled.div`
  display: flex;
  flex-direction: column;
  gap: 20px;
  width: 100%;
  margin: 0 auto;

  @media ${device.laptop} {
    margin-top: 20px;
  }
`;
