import { StaticImage } from 'gatsby-plugin-image';
import {
  GetDemoBackgroundImagesWrapper,
  GetDemoBackgroundImages,
  StyledGetDemoFormContainer,
} from './styled';
import React, { useContext, useEffect } from 'react';
import useFetchHubSpotForms from '../../hooks/useFetchHubSpotForms';
import { ClipLoader } from 'react-spinners';
import { colors } from '../../utils/colors';
import { AppContext } from '../common/Layout/Layout';

export const BackgroundImages = () => {
  return (
    <GetDemoBackgroundImagesWrapper>
      <GetDemoBackgroundImages>
        <div>
          <StaticImage
            src="../../images/GetDemo/automatedNumberPorting.png"
            placeholder="none"
            alt=""
          ></StaticImage>
        </div>
        <div>
          <StaticImage
            src="../../images/GetDemo/messageTimeRouting.png"
            placeholder="none"
            alt=""
          ></StaticImage>
        </div>
        <div>
          <StaticImage
            src="../../images/GetDemo/phoneNumberVerification.png"
            placeholder="none"
            alt=""
          ></StaticImage>
        </div>
        <div>
          <StaticImage
            src="../../images/GetDemo/advancedErrorReporting.png"
            placeholder="none"
            alt=""
          ></StaticImage>
        </div>
      </GetDemoBackgroundImages>
    </GetDemoBackgroundImagesWrapper>
  );
};

export const GetDemoForm = () => {
  const appContext = useContext(AppContext);

  useFetchHubSpotForms(
    [appContext.hubSpot.isHubSpotFormsLoaded],
    !appContext.hubSpot.isHubSpotFormsLoaded,
    appContext.hubSpot.setIsHubSpotFormsLoaded,
  );

  useEffect(() => {
    //@ts-ignore
    window.hbspt?.forms.create({
      region: 'na1',
      portalId: '21630623',
      formId: '26faf862-980b-40dc-b461-f4cda7841e47',
      target: '#hubSpotGetADemoForm',
      onFormSubmit: function () {
        //@ts-ignore
        gtag_report_conversion();
      },
      onFormReady: function (form: Element) {
        const gclidField = form.querySelector('input[name="gclid_field"]');
        if (gclidField) {
          gclidField.id = 'gclid_field'
        }
      },
    });
  }, [appContext.hubSpot.isHubSpotFormsLoaded]);

  return (
    <StyledGetDemoFormContainer id="hubSpotGetADemoForm">
      {!appContext.hubSpot.isHubSpotFormsLoaded && (
        <ClipLoader color={colors.primary.lightOrange} />
      )}
    </StyledGetDemoFormContainer>
  );
};
